import React from 'react';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import { Card } from 'antd';

const { Meta } = Card;

const Photo = ({image}) => {
  
  return (
    <Card
      hoverable
      cover={ 
        <LazyLoadImage
          src={image.url}
          width={600} /> 
      }
    >
      <Meta title={image.title} />
    </Card>
  )
}

export default Photo;