import React from 'react';
import { Row, Col, Skeleton } from 'antd'
import Photo from './Photo';

const style = { padding: '8px 0' };

const Photos = ({images}) => {
  return (
    <Row gutter={24}>
        { images ? images.map((image) => (
            <Col key={image.id} className="gutter-row" span={6}>
              <div style={style}>
                <Photo image={image}/>
              </div>
            </Col>
          )) :
          <Skeleton active/>
        }
    </Row>
  );
}

export default Photos;